import React from 'react'

const Delete = ({
  note,
  user,
  handleRemove,
}) => {
  if ((user && note.user === user.id) || note.user === null) {
    return (
      <button type='button' onClick={ handleRemove(note) }>
        Delete
      </button>
    )
  } else {
    return (
      <div>
        <span>{ user.username }</span>
        <span>{ note.user.username }</span>
      </div>
    )
  }
}

export default Delete